/**
 *
 * @title Drawer
 * @description 
 *
 */

import React, { Component } from 'react';
import Drawer from '../../src/index';
import Button from 'bee-button';

class Demo2 extends Component {
    constructor(props){
        super(props);
        this.state = {
            left:false,
            right:false,
            top:false,
            down:false
        };
    }
    open=(placement)=>{
        switch(placement){
            case 'left':
                this.setState({
                    left:true
                })
            break;
            case 'right':
                this.setState({
                    right:true
                })
            break;
            case 'top':
                this.setState({
                    top:true
                })
            break;
            case 'down':
                this.setState({
                    down:true
                })
            break;
        }
    }
    close=(placement)=>{
        switch(placement){
            case 'left':
                this.setState({
                    left:false
                })
            break;
            case 'right':
                this.setState({
                    right:false
                })
            break;
            case 'top':
                this.setState({
                    top:false
                })
            break;
            case 'down':
                this.setState({
                    down:false
                })
            break;
        }
    }
    render () {
        let { left,right,top,down } = this.state;
        return (
            <div className="demoPadding demo2">
                <div className="btnc">  
                    <Button colors="primary" onClick={()=>{this.open('left')}}></Button>
                    <Button colors="primary" onClick={()=>{this.open('right')}}></Button>
                    <Button colors="primary" onClick={()=>{this.open('top')}}></Button>
                    <Button colors="primary" onClick={()=>{this.open('down')}}></Button>
                </div>
                <Drawer className={'demo2'} placement='left' title="" show={left} onClose={()=>{this.close('left')}}>
                    <div className="con">
                        <p></p>
                        <p></p>
                        <p></p>
                        <p>~</p>                                                  
                    </div>
                </Drawer>
                <Drawer className={'demo2'} placement='right' title="" show={right} onClose={()=>{this.close('right')}}>
                    <div className="con">
                        <p></p>
                        <p></p>
                        <p></p>
                        <p>~</p>                                                  
                    </div>
                </Drawer>
                <Drawer className={'demo2'} placement='top' title="" show={top} onClose={()=>{this.close('top')}}>
                    <div className="con">
                        <p></p>
                        <p></p>
                        <p></p>
                        <p>~</p>                                                  
                    </div>
                </Drawer>
                <Drawer className={'demo2'} placement='bottom' title="" show={down} onClose={()=>{this.close('down')}}>
                    <div className="con">
                        <p></p>
                        <p></p>
                        <p></p>
                        <p>~</p>                                                  
                    </div>
                </Drawer>
            </div>
        )
    }
}

export default Demo2;



