/**
 *
 * @title Drawer
 * @description 
 *
 */

import React, { Component } from 'react';
import Drawer from '../../src/index';
import Button from 'bee-button';
import Form from 'bee-form';
import FormControl from 'bee-form-control';
import Label from 'bee-label';
import Icon from 'bee-icon';
import Checkbox from 'bee-checkbox';

const FormItem = Form.FormItem;

class Demo3 extends Component {
    constructor(props){
        super(props);
        this.state = {
            placement: 'right',
            showDrawer: false,
            title: ''
        };
        this.fPopDrawer = this.fPopDrawer.bind(this);
        this.fCloseDrawer = this.fCloseDrawer.bind(this);
    }
    fPopDrawer(){
        this.setState({
            showDrawer: true
        })
    }
    fCloseDrawer(){
        this.setState({
            showDrawer: false
        })
    }
    submit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (err) {
                console.log('', values);
            } else {
                console.log('', values)
            }
        });
    }
    handleConfirmPassword = (rule, value, callback) => {
        const { getFieldValue } = this.props.form;
        if (value && value !== getFieldValue('password')) {
            callback(<span><Icon type="uf-exc-t"></Icon><span></span></span>)
        }
        callback();
    }
    render () {
        let {placement,showDrawer,title} = this.state;
        const { getFieldProps, getFieldError } = this.props.form;
        return (
            <div className="demoPadding">
                <div className="btnc">  
                    <Button colors="primary" onClick={this.fPopDrawer}></Button>
                </div>
                <Drawer className='demo3' title={title} show={showDrawer} placement={placement} onClose={this.fCloseDrawer}>
                    <Form>
                        <FormItem>
                            <Label></Label>
                            <FormControl placeholder="(8-15)"
                                {...getFieldProps('username', {
                                    validateTrigger: 'onBlur',
                                    rules: [{
                                        required: true, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }, {
                                        pattern: /^(?!\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{8,15}$/, 
                                        message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }],
                                }) }
                            />
                            <span className='error'>
                                {getFieldError('username')}
                            </span>
                        </FormItem>

                        <FormItem>
                            <Label></Label>
                            <FormControl placeholder="" type='password'
                                {...getFieldProps('password', {
                                    validateTrigger: 'onBlur',
                                    rules: [{
                                        required: true, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }],
                                }) }
                            />
                            <span className='error'>
                                {getFieldError('password')}
                            </span>
                        </FormItem>

                        <FormItem>
                            <Label></Label>
                            <FormControl placeholder="" type='password'
                                {...getFieldProps('password2', {
                                    validateTrigger: 'onBlur',
                                    rules: [{
                                        required: true, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }, {
                                        validator: this.handleConfirmPassword
                                    }],
                                }) }
                            />
                            <span className='error'>
                                {getFieldError('password2')}
                            </span>
                        </FormItem>

                        <FormItem>
                            <Label></Label>
                            <FormControl placeholder=""
                                {...getFieldProps('email', {
                                    validateTrigger: 'onBlur',
                                    rules: [{
                                        required: true, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }, {
                                        type: 'email', message: <span><Icon type="uf-exc-t"></Icon><span></span></span>
                                    }],
                                }) }
                            />
                            <span className='error'>
                                {getFieldError('email')}
                            </span>
                        </FormItem>
                        <FormItem>
                            <Label></Label>

                            <FormControl placeholder=""
                                {...getFieldProps('phone', {
                                    validateTrigger: 'onBlur',
                                    rules: [{
                                        required: true, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>,
                                    }, {
                                        pattern: /^\d{11}$/, message: <span><Icon type="uf-exc-t"></Icon><span></span></span>
                                    }],
                                }) }
                            />

                            <span className='error'>
                                {getFieldError('phone')}
                            </span>
                        </FormItem>

                        <FormItem style={{'paddingLeft':'106px'}}>
                            <Checkbox
                                defaultChecked={this.state.checkbox}
                                {
                                ...getFieldProps('checkbox', {
                                    initialValue: false,
                                }
                                ) }
                            >
                            
                            </Checkbox>
                        </FormItem>
                        <FormItem style={{'paddingLeft':'106px'}}>
                            <Button shape="border" className="reset" style={{"marginRight":"8px"}}></Button>
                            <Button colors="primary" className="login" onClick={this.submit}></Button>
                        </FormItem>
                    </Form>
                </Drawer>
            </div>
        )
    }
}

export default Form.createForm()(Demo3);



